import React, {useState,useEffect} from 'react'
import styled from 'styled-components'
import ScrollToTop from '../../components/Signin/ScrollToTop'
import Footer from '../../components/Footer/Footer'
import BreadCrumbs from '../components/Breadcrumbs'
import NavbarBottom from '../components/NavbarBottom'
import Sidebar from '../components/Publicite/Sidebar'
import { Container,makeStyles, Grid, Typography, Box, CircularProgress, Button } from '@material-ui/core'
import {dataConseil} from '../components/data/dataConseil'
import ViewJobModal from '../components/Job/ViewJobModal'
import NavbarSearch from '../components/NavbarSearch'
import CardOne from '../components/Cards/CardOne'
import SearchBar from '../components/SearchBar/SearchBar'
import Job from '../components/Job/Job'
import CardPub from '../components/Publicite/CardPub'
import app from '../../firebase/base';
import { Fragment } from 'react'
import { Close, Replay } from '@material-ui/icons'
import { Alert } from '@material-ui/lab'
import SearchEmplois from '../components/SearchBar/SearchEmplois'
import PubSlide from '../../components/Slides/PubSlide'





const HeadSection = styled.div`
   position:fixed;
   width:100%;
   z-index:10;
   background-color:#fff;
   box-shadow: 0 6px 20px rgba(10, 10, 10, 0.1);

`
const PageContent = styled.div`
   padding-top: 225px;
   //margin-left:10px;
   margin-bottom: 20px;



`

const DivSearch = styled.div`
   display:none;
   width: 100%;

   @media screen and (max-width: 1025px){
       display:block;
    }

`
const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 2,

    },
    alert: {
        width: '100%',
        justifyContent:'center'
      },
    button: {
        margin: theme.spacing(1),
        backgroundColor: "#008647",
        color:'white',

        "&:hover":{
            backgroundColor:"#1a1359",
        }

      },
    barFilter:{
        justifyContent:'space-between'
    },
    custom:{
        textTransform: 'none',
        color:'#1a1359'
    }
  }));

const Emplois = (props) => {

    const [viewJob, setViewJob] = useState(false)
    const [emplois, setEmplois] = useState([]);
    const [loading, setLoading] = useState(true);
    const [lastDoc, setLastDoc] = useState();
    const [isEmpty, setIsEmpty] = useState(false);
    const [viewFilter, setViewFilter] = useState(false)
    const [customSearch, setCustomSearch] = useState(false);
   // const {resultJobSearch} = props.location.state


    const db = app.firestore();


    const classes = useStyles();
    const [isOpen, setIsOpen] = useState(false)
    const [etat, setEtat] = useState(null);
    const [conseils, setConseils] = useState([])

    const [etatPub, setEtatPub] = useState(null);
    const [pub, setPub] = useState([])

    const jobRef =   db.collection("offre_emploi").orderBy("date_ajout", "desc")


    const toggle = () =>{
        setIsOpen(!isOpen)
    }


    const updateState = (collections) => {
        const isCollectionEmpty = collections.size == 0;
        if(!isCollectionEmpty){
            const tempJobs = collections.docs.map((item) => ({...item.data(),
                    id:item.id,
                    postedOn:item.data().date_ajout.toDate()
                    }))
            const lastDoc = collections.docs[collections.docs.length - 1];
            setLastDoc(lastDoc)
            setEmplois((listEmplois) => [...listEmplois,...tempJobs])

            }else{
                setIsEmpty(true)
            }
            setLoading(false)
       }

      const getJobs = async () => {
            setCustomSearch(false)
            await jobRef
                .limit(5)
                .get()
                .then((collections)=>{
                    updateState(collections)

                });
      }



   const afficherPlus = async () => {
          setLoading(true)
            await  jobRef
              .startAfter(lastDoc)
              .limit(5)
              .get()
              .then((collections)=>{
                    updateState(collections)
             });

   }

   const fetchJobsCustom = async (jobSearch) => {
    setCustomSearch(true)
     const req =   await db.collection("offre_emploi")
                .where("ville_offre", "==", jobSearch.ville_offre)
                .where("secteur_activite", "==", jobSearch.secteur_activite)
                .orderBy("date_ajout", "desc")
                .limit(5)
                .get();
                const tempJob = req.docs.map((item) => ({...item.data(),
                        id:item.id,
                        postedOn:item.data().date_ajout.toDate()
                }));
                setEmplois(tempJob)
                setLoading(false)
   }

   const fetchAutherJobsCustom = async (jobSearch) => {
    setCustomSearch(true)
     const req =   await db.collection("offre_emploi")
                .where("ville_offre", "==", jobSearch.ville_offre)
                .where("secteur_activite", "==", jobSearch.secteur_activite)
                .orderBy("date_ajout", "desc")
                .limit(5)
                .get();
                const tempJob = req.docs.map((item) => ({...item.data(),
                        id:item.id,
                        postedOn:item.data().date_ajout.toDate()
                }));
                setEmplois(tempJob)
                setLoading(false)
   }

   const fetchByCity = async (city) => {
    setCustomSearch(true)
     const req =   await db.collection("offre_emploi")
                .where("ville_offre", "==", city)
                .orderBy("date_ajout", "desc")
                .limit(5)
                .get();
                const isCollectionEmpty = req.size == 0;
                if(!isCollectionEmpty){
                    const tempJobs = req.docs.map((item) => ({...item.data(),
                            id:item.id,
                            postedOn:item.data().date_ajout.toDate()
                            }))
                    const lastDoc = req.docs[req.docs.length - 1];
                    setLastDoc(lastDoc)
                    setEmplois((listEmplois) => [...listEmplois,...tempJobs])

                    }else{
                        setIsEmpty(true)
                    }
                    setLoading(false)
   }



   const fetchByState = async (state) => {
    setCustomSearch(true)
     const req =   await db.collection("offre_emploi")
                .where("province_offre", "==", state)
                .orderBy("date_ajout", "desc")
                .limit(5)
                .get();
                const isCollectionEmpty = req.size == 0;
                if(!isCollectionEmpty){
                    const tempJobs = req.docs.map((item) => ({...item.data(),
                            id:item.id,
                            postedOn:item.data().date_ajout.toDate()
                            }))
                    const lastDoc = req.docs[req.docs.length - 1];
                    setLastDoc(lastDoc)
                    setEmplois((listEmplois) => [...listEmplois,...tempJobs])

                    }else{
                        setIsEmpty(true)
                    }
                    setLoading(false)
   }

   const fetchByContrat = async (contrat) => {
    setCustomSearch(true)
     const req =   await db.collection("offre_emploi")
                .where("type_contrat", "==", contrat)
                .orderBy("date_ajout", "desc")
                .limit(5)
                .get();
               /*const tempJob = req.docs.map((item) => ({...item.data(),
                        id:item.id,
                        postedOn:item.data().date_ajout.toDate()
                }));
                setEmplois(tempJob)
                setLoading(false) */
                const isCollectionEmpty = req.size == 0;
                if(!isCollectionEmpty){
                    const tempJobs = req.docs.map((item) => ({...item.data(),
                            id:item.id,
                            postedOn:item.data().date_ajout.toDate()
                            }))
                    const lastDoc = req.docs[req.docs.length - 1];
                    setLastDoc(lastDoc)
                    setEmplois((listEmplois) => [...listEmplois,...tempJobs])

                    }else{
                        setIsEmpty(true)
                    }
                    setLoading(false)
    }

    const fetchByCategories = async (jobSearch) => {
         setCustomSearch(true)
         const req = await db.collection("offre_emploi")
                    .where("secteur_activite", "==", jobSearch.secteur_activite)
                    .orderBy("date_ajout", "desc")
                    .limit(5)
                    .get();
                    const tempJob = req.docs.map((item) => ({...item.data(),
                            id:item.id,
                            postedOn:item.data().date_ajout.toDate()
                    }));
                    setEmplois(tempJob)
                    setLoading(false)
       }


       const getConseil = async () => {
        await db.collection("postes")
           .where("categorie", "==", "conseil")
           .where("visibilite", "==", "on")
           .where("type", "==", "une")
           .onSnapshot((querySnapshot) => {
               var list = []
               querySnapshot.forEach((doc) => {
                if(doc.exists){
                    setEtat(true)
                    list.push({...doc.data(), id: doc.id })
                }else{
                    setEtat(false)
                }

               });
               setConseils(list)
           });

        }

        const getPub = async () => {
            await db.collection("publicites")
               .where("categorie", "==", "laterale")
               .where("visibilite", "==", "on")
               .onSnapshot((querySnapshot) => {
                   var list = []
                   querySnapshot.forEach((doc) => {
                    if(doc.exists){
                        setEtatPub(true)
                        list.push({...doc.data(), id: doc.id })
                    }else{
                        setEtatPub(false)
                    }

                   });
                   setPub(list)
               });

            }

     useEffect(()=> {
        //resultJobSearch.length !== 0
        //console.log(props.location.state)
        getConseil();
        getPub();
        if(props.location.state != undefined){
            const {resultJobSearch} = props.location.state
            const {ville} = props.location.state
            const {province} = props.location.state
            const {contrat} = props.location.state
            if(ville != undefined){
                fetchByCity(ville)
            }
            if(province != undefined){
                fetchByState(province)
            }
            if(contrat != undefined){
                fetchByContrat(contrat)
            }
            if(resultJobSearch !=undefined){
                fetchAutherJobsCustom(resultJobSearch)
            }

        }else{
            getJobs();
        }

      }, [])

      if(emplois.length === 0){

        return  (
            <>
            <ScrollToTop/>
            <HeadSection>
                <Sidebar  isOpen={isOpen}  toggle={toggle}/>
                <NavbarSearch  fetchJobsCustom={fetchJobsCustom} toggle={toggle}/>
               {/* <PubLaterale   toggle={toggle}/>*/}
                <NavbarBottom/>
                <BreadCrumbs/>
            </HeadSection>

            <PageContent className={classes.root}>
                    <Container >
                            <Grid container spacing={3} lg={12} md={12}  xs={12}>

                                        <DivSearch>
                                                <Grid xs={12} >
                                                    <SearchEmplois fetchJobsCustom={fetchJobsCustom}/>
                                                </Grid>
                                        </DivSearch>

                                    <Grid item   lg={8} md={6} xs={12}>

                                        {customSearch && (
                                                <Box my={2} display="column"  justifyContent="center" >
                                                     <Button onClick={getJobs} className={classes.custom}>
                                                         <Replay size={20}/>
                                                         Recharger
                                                     </Button>
                                                     <Box my={2} display="flex" justifyContent="flex-start" >

                                                          <Alert className={classes.alert} severity="info">

                                                              <h2>Aucun résulat !</h2>
                                                           </Alert>

                                                    </Box>
                                                </Box>

                                                )
                                          }
                                      </Grid>

                                        {dataConseil.slice(0,1).map((conseil,index) =>
                                           <Grid item lg={4} md={6}  xs={12}  >
                                               <CardPub/>
                                              <CardOne key={index}  {...conseil}  />

                                            </Grid>
                                        )}


                            </Grid>
                    </Container>
            </PageContent>
            <Footer/>

        </>

        )
      }

    return (
        <>
            <ScrollToTop/>
            <HeadSection>
                <Sidebar  isOpen={isOpen}  toggle={toggle}/>
                <NavbarSearch  fetchJobsCustom={fetchJobsCustom} toggle={toggle}/>
               {/* <PubLaterale   toggle={toggle}/>*/}
                <NavbarBottom/>
                <BreadCrumbs/>
            </HeadSection>

            <PageContent className={classes.root}>
                    <Container >
                            <Grid container spacing={3} lg={12} md={12}  xs={12}>

                                        <DivSearch>
                                                <Grid xs={12} >
                                                    <SearchBar fetchJobsCustom={fetchJobsCustom}/>
                                                </Grid>
                                        </DivSearch>

                                    <Grid item   lg={8} md={6} xs={12}>
                                      <Box>
                                          <Box  className={classes.barFilter} display="flex-start" mb={1}>
                                         {/* <Filtre  fetchByCategories={fetchByCategories}/>*/}
                                           <Typography variant="h6" component="h2"></Typography>

                                           {/* <JobFilter fetchByCategories={fetchByCategories} />*/}

                                          </Box>
                                        </Box>
                                        {customSearch && (
                                                <Box my={2} display="flex" justifyContent="flex-start" >
                                                     <Button onClick={getJobs} className={classes.custom}>
                                                         <Close size={20}/>
                                                         recherche personnalisée
                                                     </Button>
                                                </Box>
                                                )
                                         }
                                        {

                                            emplois.map((job,index) =>
                                                <Fragment  key={index}>
                                                    <ViewJobModal  utilisateur={job.id_utilisateur}   index={index}  job={viewJob}  closeModal={()=> setViewJob({})}/>

                                                    {<Job  utilisateur={job.id_utilisateur} open={()=>setViewJob(job)} {...job} /> }
                                                </Fragment>
                                                )

                                          }
                                          {loading && <Box display="flex" justifyContent="center" ><CircularProgress/></Box> }

                                                 {!loading && !isEmpty &&
                                                   <Box display="flex" mt={4} justifyContent="center" >
                                                        <Button onClick={afficherPlus} className={classes.button} variant="contained"  color="primary">Voir plus</Button>
                                                    </Box>
                                                 }
                                                 {isEmpty &&
                                                   <Box display="flex" mt={4} justifyContent="center" >
                                                        <h1>Il n'y a plus de résultat!</h1>
                                                    </Box>
                                                 }
                                      </Grid>


                                           <Grid item lg={4} md={6}  xs={12}>

                                               {etatPub === true &&
                                                   <>
                                                       <PubSlide  dataPub={pub} />
                                                   </>
                                               } 
                                               {etat === true &&
                                                   <>
                                                    {conseils.map((item,index) => (
                                                       <CardOne  key={index}  {...item} />
                                                    ))}
                                                   </>
                                               } 

                                            </Grid>



                            </Grid>
                    </Container>
            </PageContent>
            <Footer/>

        </>
    )
}

export default Emplois
